import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import GlobalSettings from '@/GlobalSettings'
import store from '../store'
import Category from './Category'
import PrinterGroup from './PrinterGroup'
import Allergen from './Allergen'
import StringEntity from './StringEntity'
import AttributeGroup from './AttributeGroup'
import i18n from '../i18n'

const showImage = Boolean(GlobalSettings.isShowOnTable === '0')

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    form: false,
    header: false,
  },
  image: {
    type: IKDataEntity.Types.Image,
    formConfig: {
      type: {
        root: () => store.getters.rootUrl + GlobalSettings.dishImageRoot,
      },
      required: false,
    },
    tableConfig: { filterable: false },
    displayName: 'image',
    header: showImage,
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: false,
    },
    tableConfig: { filterable: false },
  },
  code: {
    tableConfig: {
      width: '60px',
    },
    header: false,
  },
  displayCode: {
    displayName: 'code',
    form: false,
  },

  dishName: {
    displayName: 'name',
    form: false,
  },
  price: {
    type: IKDataEntity.Types.Float,
    formConfig: {
      default: '',
      overwriteRule: true,
      rule: [
        v => /^[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'Bitte geben Sie den richtigen Preis ein',
      ],
    },
  },

  langs: {
    displayName: 'name',
    type: IKDataEntity.Types.Group,
    formConfig: {
      groupName: 'name',
      default: GlobalSettings.supportLang,
      childLabelKey: 'lang',
      type: {},
    },
    tableConfig: {
      displayCondition: (item) => {
        return item.lang.toLowerCase() === GlobalSettings.getLang().toLowerCase()
      },
      displayChild: (key) => {
        return key === 'name'
      },
    },
    childKey: ['name', 'desc'],
    labelKey: 'lang',
    children: [
      StringEntity, StringEntity, StringEntity,
    ],
    header: false,
  },
  // printGroupId: {
  //   tableConfig: { filterable: false },
  //   displayName: i18n.t('printCatName'),
  //   type: IKDataEntity.Types.Option,
  //   formConfig: {
  //     default: 1,
  //     type: {
  //       selectItems: PrinterGroup.getList,
  //     },
  //   },
  // },
  printGroupId: {
    displayName: i18n.t('printCatName'),
    type: IKDataEntity.Types.Option,
    formConfig: {
      default: 1,
      type: {
        selectItems: PrinterGroup.getList,
      },
    },
  },
  extraPrintGroupIds: {
    displayName: i18n.t('extraPrintGroup'),
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        multiple: true,
        selectItems: PrinterGroup.getList,
      },
      required: false,
    },
    header: false,
  },
  categoryId: {
    displayName: '分类',
    tableConfig: { filterable: false },
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Category.getList,
        itemText: '_langsname',
      },
    },
  },
  AllergenId: {
    displayName: i18n.t('过敏源'),
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Allergen.getList,
        multiple: true,
      },
      required: false,
    },
    header: false,
  },
  attributeGroupId: {
    displayName: i18n.t('attributeGroupNames'),
    type: IKDataEntity.Types.Option,
    formConfig: {
      merge: false,
      required: false,
      type: {
        selectItems: AttributeGroup.getList,
        multiple: true,
        disabledItem: (item, editedItem) => {
          if (editedItem.inheritAttributeGroupId) {
            return ({
              ...item,
              disabled: editedItem.inheritAttributeGroupId.includes(item.id),
            })
          } else {
            return item
          }
        },
      },
    },
    header: false,
  },
  localAttributeGroupId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: AttributeGroup.getList,
        multiple: true,
      },
    },
    form: false,
    header: false,
  },
  inheritAttributeGroupId: {
    displayName: 'AttributeGroupName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: AttributeGroup.getList,
        multiple: true,
      },
    },
    form: false,
    header: false,
  },
  dishWeight: {
    displayName: 'DishWeight',
    type: IKDataEntity.Types.Integer,
    formConfig: {
      default: 1,
      required: false,
      rule: [
        v => /^[0-9]\d*$/.test(v) || 'Bitte geben Sie die ganze Zahl ein',
      ],
    },
    header: false,
  },
  isActive: {
    type: IKDataEntity.Types.Boolean,
    displayName: 'isActive',
    formConfig: {
      default: 1,
      required: false,
    },
  },
  dishAlias: {
    required: false,
    formConfig: {
      requiredNew: false,
      requiredEdit: false,
    },
    header: false,
  },

}
const config = {
  multipleAdd: function (item) {
    return hillo.postWithUploadFile('Dishes.php?op=add', {
      ...item,
      params: JSON.stringify(item),
    }, { showLoading: false })
  },
  add: function (item) {
    item.attributeGroup = item.attributeGroupId
      .filter(i => !item.inheritAttributeGroupId.includes(i)).join(',')
    item.allergenGroup = item.AllergenId.join(',')
    console.log(item.extraPrintGroupIds)
    item.extraPrintGroupIds = item.extraPrintGroupIds?.join ? item.extraPrintGroupIds.join(',') : ''
    return hillo.postWithUploadFile('Dishes.php?op=add', {
      ...item,
      params: JSON.stringify(item),
    }, { showLoading: true })
  },
  edit: function (item) {
    item.attributeGroup = item.attributeGroupId
      .filter(i => !item.inheritAttributeGroupId.includes(i)).join(',')
    item.allergenGroup = item.AllergenId.join(',')
    item.extraPrintGroupIds = item.extraPrintGroupIds?.join ? item.extraPrintGroupIds.join(',') : ''
    if (item.file) {
      item.imageExt = item.file.name.split('.')[1]
    }
    item.isActive = item.isActive ? 1 : 0
    return hillo.postWithUploadFile('Dishes.php?op=update', {
      ...item,
      params: JSON.stringify(item),
    }, { showLoading: true })
  },
  remove: function (id) {
    return hillo.post('Dishes.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Dishes.php', {
      lang: GlobalSettings.getLang(),
      onlyActive: 0,
      ...filter,
    }))
      .content.map(function (item) {
        item.displayCode = item.code.length > 5 ? item.code.slice(0, 5) + '...' : item.code
        item.id = item.dishId
        item.attributeGroupId = [
          item.inheritAttributeGroupId ? item.inheritAttributeGroupId.split(',') : [],
          item.localAttributeGroupId ? item.localAttributeGroupId.split(',') : [],
        ].flat().join(',')
        item.langs = item.langs.map(i => {
          return {
            ...i,
            desc: i.description,
          }
        })
        return item
      })
  },
}
const DishModel = IKDataEntity.ModelFactory(entity, config)
DishModel.getBuffet = async function () {
  return (await DishModel.getList()).filter(i => i.dishesCategoryTypeId === '8')
}
export default DishModel
