import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import ConsumeType from '@/model/ConsumeType'
import WeekDays from '@/model/WeekDays'
import IKUtils from 'innerken-js-utils'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    header: false,
    formConfig: {
      requiredNew: false,
    },
  },

  consumeTypeId: {
    // displayName: 'name',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: ConsumeType.getList,
        itemText: 'name',
      },
    },
  },

  fromTime: {
    type: IKDataEntity.Types.Time,
    // displayName: 'Zeit ab',
  },
  toTime: {
    type: IKDataEntity.Types.Time,
    // displayName: 'Zeit bis',
  },

  dayOfWeek: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: WeekDays.getList(),
        itemText: 'name',
        multiple: true,
      },
    },
  },
}

const config = {

  load: async function (filter) {
    return ((await hillo.get('ConsumeType.php?op=showConsumeTypeTimeSpanByConsumeType',
      ...filter,
    ))
      .content)
  },
  add: async function (item) {
    const tempItem = item
    console.log(tempItem, 'tempItem')
    for (const dayOfWeek of item.dayOfWeek) {
      tempItem.dayOfWeek = dayOfWeek
      hillo.post('ConsumeType.php?op=addConsumeTypeTimeSpan', {
        ...tempItem,
      }, {
        showLoading: true,
      })
    }
  },
  remove: function (id) {
    return hillo.post('ConsumeType.php?op=deleteConsumeTypeTimeSpan', { id: id })
  },
  edit: function (item) {
    // const tempItem = item
    // console.log(tempItem, 'edit Item')
    // for (const dayOfWeek in item.dayOfWeek) {
    //   tempItem.dayOfWeek = dayOfWeek
    //   hillo.postWithUploadFile('ConsumeType.php?op=editConsumeTypeTimeSpan', ...tempItem, {
    //     showLoading: true,
    //   })
    // }
    console.log('edit item', item)
    return hillo.postWithUploadFile('ConsumeType.php?op=editConsumeTypeTimeSpan', item, {
      showLoading: true,
    })
  },
  resetTimeSpan: async function (list) {
    IKUtils.showLoading(true)
    for (const item of list) {
      item.fromTime = '00:00:00'
      item.toTime = '23:59:59'
      hillo.postWithUploadFile('ConsumeType.php?op=editConsumeTypeTimeSpan', item, {
        showLoading: false,
      })
    }
    IKUtils.toast('OK')
  },

}

export default IKDataEntity.ModelFactory(entity, config)
