import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import ConsumeType from '@/model/ConsumeType'
import Dish from '@/model/Dish'

const entity = {
  dishesId: {
    // displayName: 'name',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Dish.getBuffet,
        itemText: '_langsname',
      },
    },
  },
  consumeTypeId: {
    type: IKDataEntity.Types.Option,
    formConfig: {
      disableEdit: true,
      type: {
        selectItems: ConsumeType.getList,
        itemText: 'name',
      },
    },
  },
  isForAdult: {
    type: IKDataEntity.Types.Boolean,
    // displayName: 'Für Erwachsene',
  },
}

const config = {

  load: async function (filter) {
    return ((await hillo.get('ConsumeType.php?op=showBuffetPriceDishToConsumeTypeByConsumeType',
      ...filter,
    ))
      .content).map(d => {
        d.id = d.dishesId
        return d
      })
  },
  add: function (item) {
    item.dishId = item.dishesId
    item.isForAdult = item.isForAdult ? 1 : 0
    // Don't ask why exists dishId and dishesId
    return hillo.postWithUploadFile('ConsumeType.php?op=setBuffetPriceDishToConsumeType', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    const uploadRemoveItem = { dishId: id }
    return hillo.post('ConsumeType.php?op=deleteBuffetPriceDishToConsumeType', uploadRemoveItem)
  },
}

export default IKDataEntity.ModelFactory(entity, config)
